.wrapper {
    margin-left: 10vw;
    display: flexbox;
    justify-content: center;
    height: fit-content;
}

.wrapper .textWholeTab{
    text-align: center;
    display: grid;

}

.wrapper .textWholeTab p{
    color: #FFCCA3;
    line-height: 1;
    margin: 0;
    font-size: 150%;
}

.wrapper .textWholeTab h1{
    font-size: 3.5vw;
    margin: 0;
    padding: 0;
    line-height: 1;
}

@media only screen and (max-width: 900px) {
    .wrapper {
        margin-left: 0;
        width: 100%;
    }
}