.whole {
    display: flex;
    flex-direction: column;
    height: 60vh;
    width: 80vw;
    border-radius: 30px 30px 0 0;
    filter: drop-shadow(10px 10px 10px rgb(3, 3, 3));
    background: linear-gradient(to right, #420573, #8335B5, #420573 33%, #8335B5, #9E3785, #B8603E, #420573 66%, #8335B5, #9E3785, #B8603E);
    border-radius: 25px;
}

.tabselector {
    display: flex;
    min-height: 15%;
    border-radius: 20px 20px 0 0;
}

.contentTabs{
    align-content: start;
    margin: 0;
    padding: 1rem 1rem;
    color: black;
    flex-grow: 1;
    display: flex;
    height: 100%;
    border-radius: 0 0 25px 25px;
}

/*für Daniel 1*/
.backgroundchange1{
    background: linear-gradient(to right, #420573, #8335B5, #9E3785, #B8603E);
}

/*für Daniel 2*/
.backgroundchange2{
    background: linear-gradient(to right, #9E3785, #420573, #8335B5, #B8603E);
}

/*für Daniel 3*/
.backgroundchange3{
    background: linear-gradient(to right, #9E3785, #B8603E, #420573, #8335B5);
}

.text-display {
    padding: 2.5% ;
    margin: 0;
    width: 95%;
    height: 85%;
    background: no-repeat linear-gradient(#0000006E, white 10% 90%, #0000006E);
    background-color: white;
    font-size: 200%;
}

.textContent{
    width: 100%;
    height: 100%;
}

@media only screen and (max-width: 900px) {
    .whole {
        width: 100%;
        height: 50vh;
    }
}