.container {
    display: flex;
}

.tab {
    display: flex;
    justify-content: center;
    text-align: center;
    margin: 0;
    flex-grow: 1;
    font-size: 1.5vw;
    color: white;
    cursor: pointer;
    width: 80vw;
}

.underline {
    position: absolute;
    top: 15%;
    width: 20%;
    height: 5px;
    background: black;
}

.selected {
    display: flex;
    text-align: center;
    margin: 0;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to right, #420573, #8335B5);
    color: white;
    border-radius: 20px 20px 0 0;
    flex-grow: 1;
}

.unselected {
    justify-content: center;
    align-items: center;
    display: flex;
    width: 100%;
    margin: 0;
    background-color: darkgrey;
    color: white;
    border-radius: 20px 20px 0 0;
    background: linear-gradient(to right, #420573, #8335B5, #9E3785, #8C4548);
}

.TabSteps{
    height: fit-content;
    width: fit-content;
    margin: 0;
    padding: 0;
}

@media only screen and (max-width: 900px) {
    .tab {
        display: flex;
        justify-content: center;
        text-align: center;
        margin: 0;
        flex-grow: 1;
        font-size: 4.5vw;
        color: white;
        cursor: pointer;
        width: 100vw;
    }    
}